
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'Title',
  props: {
    title: {
      type: String,
      default: 'No Title',
    },
    closeButton: {
      type: Boolean,
      default: false,
    },
    onClickCallback: Function,
  },
  setup(props) {
    const invokeOnClickCallback = () => {
      if (props.onClickCallback) props.onClickCallback();
    };
    return { invokeOnClickCallback };
  },
});
